import fetch from "node-fetch";
import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    server: "",
    serverUsers: "https://coopefacsa.coop:3099",
    defaultUserImage:
      "data:image/png;base64,",
    defaultRoomImage:
      "data:image/png;base64,",
    user: {
      image: "",
      id: 0,
      nombre: "",
      correo: ""
    },
    usuarios: [],
    mensajes: [],
    salas: [],
    socketIo: null
  },
  mutations: {
    setVar(state, payload) {
      Vue.set(state, payload.variable, payload.data);
    },
    addMessage(state, payload) {
      state.mensajes.push(payload);
    },
    addRoom(state,payload){
      state.salas.push(payload);
    },
    mensajesLeidos(state,payload){
      state.mensajes.forEach((m)=>{
        if(m.from==payload.from && m.to==payload.to){Vue.set(m,"estado",1)}
      })
    },
    
    updateUser(state,payload){
      let idx = state.usuarios.findIndex(u=>{return u.id==payload.id})
      if(idx>=0){
        Object.keys(payload).forEach(k=>{
          Vue.set(state.usuarios[idx],k,payload[k])
        })
      }
    }
  },
  actions: {
    getUsers({ state, commit }, payload) {
      let options = {
        method: "post",
        headers: { "content-type": "application/json" },
        body: JSON.stringify({
          tabla: "usuarios",
          token: state.user.token
        })
      };
      fetch(`${state.serverUsers}/getData`, options)
        .then(res => {
          return res.json();
        })
        .then(json => {
          if (!json.errno) {
            commit("setVar", {
              variable: "usuarios",
              data: json
            });
            payload(json);
          } else {
            console.log(json, "Error al obtener usuarios");
          }
        });
    },
    getSalas({ state, commit }, payload) {
      let options = {
        method: "post",
        headers: { "content-type": "application/json" },
        body: JSON.stringify({
          tabla: "salas",
          token: state.user.token
        })
      };
      fetch(`${state.server}/getData`, options)
        .then(res => {
          return res.json();
        })
        .then(json => {
          if (!json.errno) {
            commit("setVar", {
              variable: "salas",
              data: json
            });
          } else {
            console.log(json, "Error al obtener salas");
          }

          payload(json);
        });
    },
    getMessages({ state, commit }) {
      let options = {
        method: "post",
        headers: { "content-type": "application/json" },
        body: JSON.stringify({
          salas: state.salas
            .map(s => {
              return s.id;
            })
            .join(","),
          token: state.user.token
        })
      };
      fetch(`${state.server}/getMessages`, options)
        .then(res => {
          return res.json();
        })
        .then(json => {
          if (!json.errno) {
            commit("setVar", {
              variable: "mensajes",
              data: json
            });
          } else {
            console.log(json, "Error al obtener mensajes");
          }
        });
    }
  },
  modules: {}
});
